<template>
  <div class="search-wrapper">
    <div class="search-box">
      <div class="search-input">
        <input type="text" ref="query"  v-model="query" :placeholder="placeholder" class="fl">
        <i @click="onClear" v-show="query" class="icon-close">删除</i>
      </div>
      <div class="search-btn fl"  @click="onSearch">搜索</div>
    </div>
    <div class="search-content">
       <ul>
         <li v-for="(item,index) in matchlist" :key="index">{{item.name}}</li>
       </ul>
    </div>
  </div>
</template>

<script>
import { debounce } from '@/assets/js/util'
export default {
  name: 'BaseSearchBox',
  props: {
    placeholder: {
      type: String,
      default: '请输入你想搜索的关键字'
    },
    matchlist: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  data () {
    return {
      query: ''
    }
  },
  methods: {
    onClear () {
      this.query = ''
    },
    onSearch () {
      this.$emit('search', this.query)
    },
    blur () {
      this.$refs.query.blur()
    }
  },
  created () {
    this.$watch('query', debounce((newQuery) => {
      // this.$emit('query', newQuery)
      this.$emit('matchSearch', newQuery)
    }, 200))
  }
}
</script>

<style scoped lang="scss">
  .search-wrapper{
    .search-box{
      .search-input{

      }
    }
  }
</style>
